<template>
    <md-list class="md-primary">
      <md-list-item to="/profile/info">
        <span class="md-list-item-text"></span>
      </md-list-item>
      <md-list-item md-expand>
        <span class="md-list-item-text"></span>
        <md-list slot="md-expand" :md-expand.sync="$route.name === 'ProfileProject'">
          <md-list-item class="md-inset" to="/profile/project/join"></md-list-item>
          <md-list-item class="md-inset" to="/profile/project/publish"></md-list-item>
          <md-list-item class="md-inset" to="/profile/project/complete"></md-list-item>
        </md-list>
      </md-list-item>
      <md-list-item to="/profile/message">
        <span class="md-list-item-text"></span>
      </md-list-item>
    </md-list>
</template>

<script>
export default {
  name: 'MenuBar',
  data () {
    return {}
  }
}
</script>

<style scoped>
  .md-list {
    width: 320px;
  }
</style>



